import React from 'react'

const TablaEmpleado = (props) => (
  <table>
    <thead>
      <tr>
        <th>Nombre</th>
        <th>Vehiculo</th>
        <th>Ciudad</th>
        <th>Accion</th>        
      </tr>
    </thead>
    <tbody>
        {
            props.users.length > 0 ? (
                props.users.map(user => (
                    <tr key={user.id}>
                        <td>{user.nombre}</td>
                        <td>{user.vehiculo}</td>                        
                        <td>{user.ciudad}</td>
                        <td>
                        <button 
                            className="button muted-button"
                            onClick={() => {
                                props.editRow(user)
                            }}
                            >
                            Edit
                        </button>
                        <button 
                            className="button muted-button"
                            onClick={() => props.deleteUser(user.id)}
                            >
                            Delete
                        </button>
                        </td>
                    </tr>
                ))
            ) : (
                <tr>
                    <td colSpan={3}>Sin Usuarios</td>
                </tr>
            )
        }

      
    </tbody>
  </table>
)

export default TablaEmpleado