import React from 'react';
import { useForm } from 'react-hook-form'

const AgregarDato = (props) => {

    const {register, errors, handleSubmit} = useForm();

    const onSubmit = (data, e) => {
        data.id = null
        console.log(data)
        props.addUser(data)
        e.target.reset();
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            <label>Nombre</label>
            <input                
                type="text" 
                name="nombre"
                placeholder="Escribe tu nombre"
                ref={register({required: {value: true, message: '--Nombre Requerido--'}})}
                />
            <div>
                {errors?.nombre?.message}
            </div>
            <label>Vehiculo</label>
            <input 
                type="text" 
                name="vehiculo"
                placeholder="Escribe tu Clase de Vehiculo" 
                ref={register({required: {value: true, message: '--Vehiculo Requerido--'}})}
                />
            <div>
                {errors?.vehiculo?.message}
            </div>
            <label>Ciudad</label>
            <input 
                type="text" 
                name="ciudad"
                placeholder="Escribe tu Ciudad" 
                ref={register({required: {value: true, message: '--Ciudad Requerida--'}})}
                />
            <div>
                {errors?.ciudad?.message}
            </div>
            <button type="submit">Agregar Empleado</button>
        </form>
    );
}
 
export default AgregarDato;