import React from 'react'
import { useForm } from 'react-hook-form'

const Edito = (props) => {

    const {register, errors, handleSubmit, setValue} = useForm({
        defaultValues: props.currentUser
    });

    setValue('nombre', props.currentUser.nombre)
    setValue('vehiculo', props.currentUser.vehiculo)
    setValue('ciudad', props.currentUser.ciudad)

    const onSubmit = (data, e) => {
        data.id = props.currentUser.id
        console.log(data)
        props.updateUser(props.currentUser.id, data)
        e.target.reset()
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            <label>Nombre</label>
            <input 
                type="text" 
                name="nombre"
                ref={register({required: {value: true, message: 'Valor requerido'}})}
                />
            <div>
                {errors?.nombre?.message}
            </div>
            <label>Vehiculo</label>
            <input 
                type="text" 
                name="vehiculo" 
                ref={register({required: {value: true, message: 'Valor requerido'}})}
                />
            <div>
                {errors?.vehiculo?.message}
            </div>
            <label>Ciudad</label>
            <input 
                type="text" 
                name="ciudad" 
                ref={register({required: {value: true, message: 'Valor requerido'}})}
                />
            <div>
                {errors?.ciudad?.message}
            </div>
            <button type="submit">Editar Usuario</button>
            <button onClick={() => props.setEditing(false)} className="button muted-button">
                Cancelar
            </button>
        </form>
    );
}
 
export default Edito;