import React, {useState} from 'react';
import TablaEmpleado from './components/TablaEmpleado';
import AgregarDato from './components/AgregarDato';
import Edito from './components/Edito';
import { v4 as uuidv4 } from 'uuid'; // agrega un id aleatorio para que no se repitan

function App() {

  // Agregar usuarios
  const empleadoDatos = [
    { id: uuidv4(), nombre: 'David', vehiculo: 'Moto', ciudad: 'Cali' },
    { id: uuidv4(), nombre: 'Laura', vehiculo: 'Automovil', ciudad: 'BogotÃ¡' },
    { id: uuidv4(), nombre: 'Ben', vehiculo: 'Camion', ciudad: 'Bucaramanga' },

  ]

  const [users, setUsers] = useState(empleadoDatos)

  const addUser = (user) => {
    user.id = uuidv4()
    console.log(user)
    setUsers([
      ...users,
      user
    ])
  }

  // Eliminar usuario
  const deleteUser = id => {
    setUsers(users.filter(user => user.id !== id))
  }

  // Editar usuario
  const [editing, setEditing] = useState(false)

  const initialFormState = { id: null, nombre: '', vehiculo: '', ciudad: '', }
  const [currentUser, setCurrentUser] = useState(initialFormState)

  const editRow = user => {
    setEditing(true) 
    setCurrentUser({ id: user.id, nombre: user.nombre, vehiculo: user.vehiculo, ciudad: user.ciudad })
  }

  const updateUser = (id, updatedUser) => {
    setEditing(false)
    setUsers(users.map(user => (user.id === id ? updatedUser : user)))
  }

  return (
    <div className="container">
      <h1>CRUD- Empresa de Transporte</h1>      
      <div className="flex-row">
        <div className="flex-large">
        {editing ? (
          <div>
            <h2>Editar Empleado</h2>
            <Edito 
              setEditing={setEditing}
              currentUser={currentUser}
              updateUser={updateUser}
            />
          </div>
        ) : (
          <div>
            <h2>Agregar Empleado</h2>
            <AgregarDato addUser={addUser}  />
          </div>
        )}
      </div>
        <div className="flex-large">
          <h2>Mostrar Empleado</h2>
          <TablaEmpleado users={users} deleteUser={deleteUser} editRow={editRow} />
        </div>
      </div>
    </div>
  );
}

export default App;
